<template lang="html">
  <div class="container">
    <div class="row text-center">
      <div class="col">
        <h2 class="text-center">Eliminar Libro</h2>
        <hr>
      </div>

      <div class="row">
        <div class="col">
          <h3>¿ Estas seguro de eliminar el libro?</h3>
          <p>Titulo: {{ this.book.title }}</p>
          <p>Descripción: {{ this.book.description }}</p>
        </div>
      </div>

      <div class="row">
        <div class="col">
          <b-button @click="deleteBook" variant="danger">Eliminar</b-button>
        </div>
      </div>

    </div>
  </div>
</template>

<script>
import axios from 'axios'
axios.defaults.xsrfHeaderName = "X-CSRFTOKEN"
axios.defaults.xsrfCookieName = "csrftoken"
import swal from 'sweetalert'


export default {
  name: "DeleteBook",
  data() {
    return {
      bookId: this.$route.params.bookId,
      book: {
        title: '',
        description: '',
      }
      
    };
  },
  methods: {

    deleteBook() {
      const path = `${process.env.VUE_APP_BASE_URL}books/${this.bookId}/`
      axios.delete(path)
      .then(res => {
        console.log(res)
        this.$router.push({name: 'ListBook'})        
      })
      .catch(err => { 
        console.error(err); 
        swal("No es posible eliminar el libro", "", "error")
      })
      
    },

    getBook() {
      const path = `${process.env.VUE_APP_BASE_URL}books/${this.bookId}/`
      axios.get(path)
      .then(res => {
        this.book.title = res.data.title
        this.book.description = res.data.description
      })
      .catch(err => {
        console.error(err); 
      })
    }

  },
  created() {
    this.getBook()
  },
};
</script>

<style lang="css">
</style>



