<template lang="html">
  <div class="container">
    <div class="row">
      <div class="col">
        <h2 class="text-center">Editar Libro</h2>
      </div>

      <div class="row">
        <div class="col">
          <div class="card">
            <div class="card-body">
              <form @submit="onSubmit">

                <div class="row mb-3">
                  <label for="title" class="col-sm-2 col-form-label">Titulo</label>
                  <div class="col-sm-6">
                    <input type="text" name="title" class="form-control"
                    placeholder="Titulo" v-model.trim="form.title">
                  </div>
                </div>
                <div class="row mb-3">
                  <label for="description" class="col-sm-2 col-form-label">Descripcion</label>
                  <div class="col-sm-6">
                    <textarea name="description" rows="3" class="form-control" placeholder="Descripcion"
                    v-model.trim="form.description"></textarea>
                  </div>
                </div>

                <div class="row">
                  <div class="col text-left">
                    <b-button type="submit" variant="primary">
                      <b-icon icon="cloud-upload" aria-hidden="true"></b-icon> Editar
                    </b-button>
                    
                    <router-link :to="{ name: 'ListBook' }" custom v-slot="{ navigate }">
                      <b-button @click="navigate" class="mx-1 btn-large-space">
                        <b-icon icon="arrow-return-left" aria-hidden="true"></b-icon> Cancelar
                      </b-button>
                    </router-link>

                  </div>
                </div>

              </form>
              

            </div>      
          </div>
        </div>
      </div>

    </div>
  </div>
</template>

<script>
import axios from 'axios'
axios.defaults.xsrfHeaderName = "X-CSRFTOKEN"
axios.defaults.xsrfCookieName = "csrftoken"
import swal from 'sweetalert'


export default {
  name: "EditBook",
  data() {
    return {
      bookId: this.$route.params.bookId,
      form: {
        title: '',
        description: '',
      }
    };
  },
  methods: {

    onSubmit(evt) {
      evt.preventDefault()
      const path = `${process.env.VUE_APP_BASE_URL}books/${this.bookId}/`
      axios.put(path, this.form)
      .then(res => {
        this.form.title = res.data.title
        this.form.description = res.data.description
        
        swal("Libro actualizado exitosamente", "", "success")
      })
      .catch(err => { 
        console.log(err)
        console.log("***************")
        console.error(err)
      })
      
    },

    getBook() {
      const path = `${process.env.VUE_APP_BASE_URL}books/${this.bookId}/`
      axios.get(path)
      .then(res => {
        this.form.title = res.data.title
        this.form.description = res.data.description
      })
      .catch(err => {
        console.error(err); 
      })
    }

  },
  created() {
    this.getBook()
  },
};
</script>

<style lang="css">
</style>



