<template lang="html">
  <div class="container">
    <div class="row">
      <div class="col text-left">

        <h2 class="text-center">Listado de libros</h2>
        <div class="d-grid gap-2">
          <router-link :to="{ name:'NewBook' }" custom v-slot="{ navigate }">
            <b-button block class="mx-1" @click="navigate" variant="primary">
              <b-icon icon="plus-circle" aria-hidden="true"></b-icon> Nuevo Libro
            </b-button>
          </router-link>
          
          
        </div>
        
        
        
        <div class="col-md-12">
          <b-table striped hover :items="books" :fields="fields">

            
              <template v-slot:cell(action)="data">
                <router-link :to="{ name:'EditBook', params: {bookId: data.item.id} }" custom v-slot="{ navigate }">
                  <b-button @click="navigate" size="sm" class="mx-1" variant="warning">Editar</b-button>
                </router-link>

                <router-link :to="{ name:'DeleteBook', params: {bookId: data.item.id} }" custom v-slot="{ navigate }">
                  <b-button @click="navigate" size="sm" class="mx-1" variant="danger">Eliminar</b-button>
                </router-link>
                
              </template>


          </b-table>
        </div>
        
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: "ListBook",
  data() {
    return {
      fields: [
        { key: 'title', label: 'Titulo' },
        { key: 'description', label: 'Descripcion' },
        { key: 'action', label: 'Accion' },
      ],
      books: [],
    };
  },
  methods: {

    getBooks() {
      const path = `${process.env.VUE_APP_BASE_URL}books/`
      axios.get(path)
      .then(res => {
        this.books = res.data
      })
      .catch(err => {
        console.error(err); 
      })
    }

  },
  created() {
    this.getBooks()    
  },
  mounted() {
  },
};
</script>

<style lang="css">

</style>



